#header {
    width: 100%;
    height: 100px;
    display: flex;
    flex-direction: row;
    align-items: center;
}
#logo {
    width: 30%;
    font-size: 30px;
    font-family: 'Epilogue_Title';
}
#inforHeader {
    width: 60%;
    display: flex;
    justify-content: flex-end;
    gap: 20px;
}
#inforHeader > a {
    font-family: 'Epilogue_Title';
    font-size: 16px;
}

#iconMenu {
    width: 10%;
    text-align: center;
}
.menu {
    position: absolute;
    top: 100px;
    right: 3%;
    width: 10%;
    height: 40vh;
    text-align: right;
    display: none;
}
#contentMenu {
    padding-bottom: 20px;
}

