
section:nth-child(1) {
    transform: translateY(50px);
    display: flex;
    flex-direction: row;
    gap: 5%;
}
#picSection1 {
    width: 70%;
    height: 75vh;
    background-image: url('./images/pic1.jpg');
    background-size: cover;
}
#contentSection1 {
    width: 30%;
    display: flex;
    flex-direction: column;
    text-align: left;
    justify-content: center;
}
#titleSection1 {
    color: rgb(183, 229, 251);
    font-size: 60px;
    font-family: 'Epilogue_Title';
}
#subSection1 {
    padding-top: 20px;
    font-size: 16px;
}
#contentSection1 > a {
    font-family: 'Epilogue_Title';
    font-size: larger;
    padding-top: 20px;
    width: max-content;
}
#contentSection1 > a:hover {
    color: rgb(255, 99, 37);
}


section:nth-child(2) {
    transform: translateY(150px);
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 5%;
}
#picSection2 {
    width: 40%;
    height: 100vh;
    background-image: url('./images/pic2.jpeg');
    background-size: cover;
}
#contentSection2 {
    width: 40%;
}
#titleSection2 {
    font-size: 50px;
    color: rgb(183, 229, 251);
}
#subSection2 {
    padding-top: 20px;
    font-size: 28px;
    line-height: 1.5;
}

section:nth-child(3) {
    transform: translateY(250px);
}
#miniTitleSection3 , #titleSection3 {
    width: 100%;
    height: fit-content;
    text-align: center;
}
#miniTitleSection3 {
    font-size: larger;
}
#titleSection3 {
    padding-top: 20px;
    font-size: 70px;
    color: rgb(183, 229, 251);
}

#boxSection3 {
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    gap: 30px;
}
#boxSection3 > #box {
    margin-top: 20vh;
    width: 30%;
    height: 70vh;
    
}
#box > #title {
    font-size: 32px;
    color: rgb(183, 229, 251);
    padding-top: 55%;
}
#box > #sub {
    padding-top: 20px;
    line-height: 1.5;
}
#box {
    position: relative;
}
#square1 , #square2 {
    width: 100px;
    height: 100px;
    position: absolute;
    transition: .8s ease-in-out;
}
#square1 {
    background-color: aqua;
}
#square2 {
    background-color: brown;
}
#box:nth-child(1) > #square1 {
    background-color: blue;
    
}
#box:nth-child(1) > #square2 {
    top: 50px;
    left: 50px;
    background: transparent;
    border: 1px white solid;
}
#box:nth-child(1):hover > #square1 {
    transform: translate(50px, 50px) rotate(45deg);
}
#box:nth-child(2) > #square1 {
    top: 10px;
    transform: rotate(45deg);
    background: transparent;
    border: 1px white solid;
    z-index: 2;
}
#box:nth-child(2) > #square2 {
    top: 60px;
    left: 50px;
    z-index: 1;
    background-color: orangered;
}
#box:nth-child(2):hover > #square2 {
    transform: translate(30px, -35px) rotate(45deg);
    width: 75px;
    height: 75px;
}
#box:nth-child(3) > #square1 {
    top: 30px;
    left: 30px;
    background: transparent;
    border: 1px white solid;
    z-index: 2;
}
#box:nth-child(3) > #square2 {
    left: 30px;
    top: 30px;
    transform: scale(0.8);
    background-color: rgb(255, 64, 96);
    z-index: 1;
}
#box:nth-child(3):hover > #square2 {
    transform: scale(1) rotate(45deg);
}

section:nth-child(4) {
    transform: translateY(350px);
    position: relative;
    height: 100vh;
}
#picSection4 {
    position: absolute;
    top: 5vh;
    left: 15%;
    width: 80%;
    height: 90vh;
    background-image: url('./images/pic3.jpg');
    background-size: cover;
    position: relative;
}
#titleSection4 {
    position: absolute;
    width: 600px;
    transform: rotate(90deg);
    left: -330px;
    top: 50%;
    font-size: 35px;
}


section:nth-child(5) {
    transform: translateY(450px);
    background-color: rgb(83, 10, 107);
    width: 100%;
    height: 100vh;
    
}
#boxSection5 {
    width: 100%;
    display: flex;
    flex-direction: row;
}
#contentBoxSection5 {
