}
#contentBoxSection5:nth-child(1) > #imageBoxSection5 {
    background-image: url('./images/pic4.jpeg');
}
#contentBoxSection5:nth-child(2) > #imageBoxSection5 {
    background-image:url('./images/pic5.jpeg');
}
#contentBoxSection5:nth-child(3) > #imageBoxSection5 {
    background-image: url('./images/pic6.jpeg');
}
#contentBoxSection5:nth-child(4) > #imageBoxSection5 {
    background-image: url('./images/pic7.jpg');
}

#imageBoxSection6 {
    width: 400px;
    height: 400px;
    background-size: cover;
}
#contentBoxSection6:nth-child(1) > #imageBoxSection6 {
    background-image: url('./images/pic8.jpg');
}
#contentBoxSection6:nth-child(2) > #imageBoxSection6 {
    background-image: url('./images/pic9.jpg');
}
#contentBoxSection6:nth-child(3) > #imageBoxSection6 {
    background-image: url('./images/pic10.jpeg');
}
#contentBoxSection6:nth-child(4) > #imageBoxSection6 {
    background-image: url('./images/pic11.jpeg');
}
#contentBoxSection6:nth-child(5) > #imageBoxSection6 {
    background-image: url('./images/pic12.jpg');
}
#contentBoxSection6:nth-child(6) > #imageBoxSection6 {
