}

#imageSection7 {
    width: 150px;
    height: 150px;
    background-image: url('./images/pic3.jpg');
    border-radius: 100px;
    background-size: cover;
}

#imageBoxSection8 {
    width: 650px;
    height: 400px;
    background-size: cover;
}
#boxContentSection8:nth-child(1) > #imageBoxSection8 {
    background-image: url('./images/pic14.jpg');
}
#boxContentSection8:nth-child(2) > #imageBoxSection8 {
    background-image: url('./images/pic15.jpg');
}
#boxContentSection8:nth-child(3) > #imageBoxSection8 {
    background-image: url('./images/pic16.jpg');
}
#boxContentSection8:nth-child(4) > #imageBoxSection8 {
