@font-face {
  font-family: 'Epilogue_Title';
  src: url(/my-test-project/static/media/Epilogue-Medium.ef359a4e.ttf);
}
@font-face {
  font-family: 'Epilogue_Sub';
  src: url(/my-test-project/static/media/Epilogue-Black.bb7e1465.ttf);
}
body {
  margin: 0;
  padding: 0;
  background-color: rgb(14, 12, 32);
}
* {
  font-family: 'Epilogue_Title';
  font-size: 18px;
  color:rgb(213, 213, 213);
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#header {
    width: 100%;
    height: 100px;
    display: flex;
    flex-direction: row;
    align-items: center;
}
#logo {
    width: 30%;
    font-size: 30px;
    font-family: 'Epilogue_Title';
}
#inforHeader {
    width: 60%;
    display: flex;
    justify-content: flex-end;
    grid-gap: 20px;
    gap: 20px;
}
#inforHeader > a {
    font-family: 'Epilogue_Title';
    font-size: 16px;
}

#iconMenu {
    width: 10%;
    text-align: center;
}
.menu {
    position: absolute;
    top: 100px;
    right: 3%;
    width: 10%;
    height: 40vh;
    text-align: right;
    display: none;
}
#contentMenu {
    padding-bottom: 20px;
}



section:nth-child(1) {
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
    display: flex;
    flex-direction: row;
    grid-gap: 5%;
    gap: 5%;
}
#picSection1 {
    width: 70%;
    height: 75vh;
    background-image: url(/my-test-project/static/media/pic1.3c59ec89.jpg);
    background-size: cover;
}
#contentSection1 {
    width: 30%;
    display: flex;
    flex-direction: column;
    text-align: left;
    justify-content: center;
}
#titleSection1 {
    color: rgb(183, 229, 251);
    font-size: 60px;
    font-family: 'Epilogue_Title';
}
#subSection1 {
    padding-top: 20px;
    font-size: 16px;
}
#contentSection1 > a {
    font-family: 'Epilogue_Title';
    font-size: larger;
    padding-top: 20px;
    width: -webkit-max-content;
    width: max-content;
}
#contentSection1 > a:hover {
    color: rgb(255, 99, 37);
}


section:nth-child(2) {
    -webkit-transform: translateY(150px);
            transform: translateY(150px);
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    grid-gap: 5%;
    gap: 5%;
}
#picSection2 {
    width: 40%;
    height: 100vh;
    background-image: url(/my-test-project/static/media/pic2.f4928ff0.jpeg);
    background-size: cover;
}
#contentSection2 {
    width: 40%;
}
#titleSection2 {
    font-size: 50px;
    color: rgb(183, 229, 251);
}
#subSection2 {
    padding-top: 20px;
    font-size: 28px;
    line-height: 1.5;
}

section:nth-child(3) {
    -webkit-transform: translateY(250px);
            transform: translateY(250px);
}
#miniTitleSection3 , #titleSection3 {
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    text-align: center;
}
#miniTitleSection3 {
    font-size: larger;
}
#titleSection3 {
    padding-top: 20px;
    font-size: 70px;
    color: rgb(183, 229, 251);
}

#boxSection3 {
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    grid-gap: 30px;
    gap: 30px;
}
#boxSection3 > #box {
    margin-top: 20vh;
    width: 30%;
    height: 70vh;
    
}
#box > #title {
    font-size: 32px;
    color: rgb(183, 229, 251);
    padding-top: 55%;
}
#box > #sub {
    padding-top: 20px;
    line-height: 1.5;
}
#box {
    position: relative;
}
#square1 , #square2 {
    width: 100px;
    height: 100px;
    position: absolute;
    transition: .8s ease-in-out;
}
#square1 {
    background-color: aqua;
}
#square2 {
    background-color: brown;
}
#box:nth-child(1) > #square1 {
    background-color: blue;
    
}
#box:nth-child(1) > #square2 {
    top: 50px;
    left: 50px;
    background: transparent;
    border: 1px white solid;
}
#box:nth-child(1):hover > #square1 {
    -webkit-transform: translate(50px, 50px) rotate(45deg);
            transform: translate(50px, 50px) rotate(45deg);
}
#box:nth-child(2) > #square1 {
    top: 10px;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    background: transparent;
    border: 1px white solid;
    z-index: 2;
}
#box:nth-child(2) > #square2 {
    top: 60px;
    left: 50px;
    z-index: 1;
    background-color: orangered;
}
#box:nth-child(2):hover > #square2 {
    -webkit-transform: translate(30px, -35px) rotate(45deg);
            transform: translate(30px, -35px) rotate(45deg);
    width: 75px;
    height: 75px;
}
#box:nth-child(3) > #square1 {
    top: 30px;
    left: 30px;
    background: transparent;
    border: 1px white solid;
    z-index: 2;
}
#box:nth-child(3) > #square2 {
    left: 30px;
    top: 30px;
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    background-color: rgb(255, 64, 96);
    z-index: 1;
}
#box:nth-child(3):hover > #square2 {
    -webkit-transform: scale(1) rotate(45deg);
            transform: scale(1) rotate(45deg);
}

section:nth-child(4) {
    -webkit-transform: translateY(350px);
            transform: translateY(350px);
    position: relative;
    height: 100vh;
}
#picSection4 {
    position: absolute;
    top: 5vh;
    left: 15%;
    width: 80%;
    height: 90vh;
    background-image: url(/my-test-project/static/media/pic3.cd1301d6.jpg);
    background-size: cover;
    position: relative;
}
#titleSection4 {
    position: absolute;
    width: 600px;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    left: -330px;
    top: 50%;
    font-size: 35px;
}


section:nth-child(5) {
    -webkit-transform: translateY(450px);
            transform: translateY(450px);
    background-color: rgb(83, 10, 107);
    width: 100%;
    height: 100vh;
    
}
#boxSection5 {
    width: 100%;
    display: flex;
    flex-direction: row;
}
#contentBoxSection5 {
    width: 25%;
}
#imageBoxSection5 {
    width: 100%;
    height: 50vh;
    background-size: cover;
}
#contentBoxSection5:nth-child(1) > #imageBoxSection5 {
    background-image: url(/my-test-project/static/media/pic4.5950816e.jpeg);
}
#contentBoxSection5:nth-child(2) > #imageBoxSection5 {
    background-image:url(/my-test-project/static/media/pic5.d9151b23.jpeg);
}
#contentBoxSection5:nth-child(3) > #imageBoxSection5 {
    background-image: url(/my-test-project/static/media/pic6.6ff20912.jpeg);
}
#contentBoxSection5:nth-child(4) > #imageBoxSection5 {
    background-image: url(/my-test-project/static/media/pic7.7fbba3c4.jpg);
}

#imageBoxSection6 {
    width: 400px;
    height: 400px;
    background-size: cover;
}
#contentBoxSection6:nth-child(1) > #imageBoxSection6 {
    background-image: url(/my-test-project/static/media/pic8.99e99b5a.jpg);
}
#contentBoxSection6:nth-child(2) > #imageBoxSection6 {
    background-image: url(/my-test-project/static/media/pic9.c5ced750.jpg);
}
#contentBoxSection6:nth-child(3) > #imageBoxSection6 {
    background-image: url(/my-test-project/static/media/pic10.7c03e2b4.jpeg);
}
#contentBoxSection6:nth-child(4) > #imageBoxSection6 {
    background-image: url(/my-test-project/static/media/pic11.1442e085.jpeg);
}
#contentBoxSection6:nth-child(5) > #imageBoxSection6 {
    background-image: url(/my-test-project/static/media/pic12.7fbba3c4.jpg);
}
#contentBoxSection6:nth-child(6) > #imageBoxSection6 {
    background-image: url(/my-test-project/static/media/pic13.bdc654a6.jpg);
}

#imageSection7 {
    width: 150px;
    height: 150px;
    background-image: url(/my-test-project/static/media/pic3.cd1301d6.jpg);
    border-radius: 100px;
    background-size: cover;
}

#imageBoxSection8 {
    width: 650px;
    height: 400px;
    background-size: cover;
}
#boxContentSection8:nth-child(1) > #imageBoxSection8 {
    background-image: url(/my-test-project/static/media/pic14.b4630fcb.jpg);
}
#boxContentSection8:nth-child(2) > #imageBoxSection8 {
    background-image: url(/my-test-project/static/media/pic15.3347282b.jpg);
}
#boxContentSection8:nth-child(3) > #imageBoxSection8 {
    background-image: url(/my-test-project/static/media/pic16.a1dfb39e.jpg);
}
#boxContentSection8:nth-child(4) > #imageBoxSection8 {
    background-image: url(/my-test-project/static/media/pic17.f811f5ed.jpg);
}
